import dayjs from "dayjs"

//
const isNull = (data)=>{
  if(!data){
    return true
  }
  if(JSON.stringify(data) == '{}'){
    return true
  }
  if(JSON.stringify(data) == '[]'){
    return true
  }
}


const filterTime = (val,format = 'YYYY-MM-DD') => {
  if(!isNull(val)){
    val = parseInt(val) * 1000 // *10
    return dayjs(val).format(format)
  }else{
    return "--"
  }
}


export default filterTime